html.hc-nav-yscroll {
    overflow-y: scroll;
}
body.hc-nav-open {
    overflow: visible;
    position: fixed;
    width: 100%;
    min-height: 100%;
}
.hc-offcanvas-nav {
    visibility: hidden;
    display: none;
    position: fixed;
    top: 0;
    height: 100%;
    z-index: 9999;
}
.hc-offcanvas-nav.is-ios * {
    cursor: pointer !important;
}
.hc-offcanvas-nav .nav-container {
    position: fixed;
    z-index: 9998;
    top: 0;
    height: 100%;
    max-width: 100%;
    max-height: 100%;
    box-sizing: border-box;
    transition: transform 0.4s ease;
}
.hc-offcanvas-nav .nav-wrapper {
    width: 100%;
    height: 100%;
    box-sizing: border-box;
    -ms-scroll-chaining: none;
    overscroll-behavior: none;
}
.hc-offcanvas-nav .nav-content {
    height: 100%;
}
.hc-offcanvas-nav .nav-wrapper-0 > .nav-content {
    overflow: scroll;
    overflow-x: visible;
    overflow-y: auto;
    box-sizing: border-box;
}
.hc-offcanvas-nav ul {
    list-style: none;
    margin: 0;
    padding: 0;
}
.hc-offcanvas-nav li {
    position: relative;
    display: block;
}
.hc-offcanvas-nav li.level-open > .nav-wrapper {
    visibility: visible;
}
.hc-offcanvas-nav li:not(.custom-content) a {
    position: relative;
    display: block;
    box-sizing: border-box;
    cursor: pointer;
}
.hc-offcanvas-nav li:not(.custom-content) a[disabled] {
    cursor: not-allowed;
}
.hc-offcanvas-nav li:not(.custom-content) a,
.hc-offcanvas-nav li:not(.custom-content) a:hover {
    text-decoration: none;
}
.hc-offcanvas-nav input[type="checkbox"] {
